import Service from './Service';
import Config from "../utils/Config";

export default class UsersService extends Service {

    static userEntity() {
        return {
            _id: '',
            email: '',
            username: '',
            directory: '',
            password: '',
            isAdmin: false,
            groups: []
        }
    }
    /**
     * Возвращает юзера тек. сессии
     * @return {Promise}    then(data)
     * data ~ @see userEntity()
     */
    getCurrent() {

        let callback = function (response) {
            if (typeof response.data !== 'object')
                document.location.href = Config.data.api.http.authURL;


            response.data.groups = response.data.groups.map(item => item.name);

            return response.data;
        }

        return this.transport.request(`auth/info`, {}, callback, 'get',
            { withCredentials: true, crossDomain: true, origin: Config.data.api.http.baseURL });
        // @TODO вызов апи нужен здесь вместо заглушки
        /*return new Promise((resolve) => {
            let user = UsersService.userEntity();
            user.roles = [ UsersService.ROLE_ADMIN_BUSINESS, UsersService.ROLE_ADMIN_TECH ];
            resolve(user);
        });*/
    }
    /**
     * Возвращает список ролей
     * @return {Promise}    then(data)
     */
    getGroupsList() {
        return new Promise((resolve) => {
            resolve([ 'admin_tech', 'admin_business' ]);
        })
    }
}